<template>
  <div>
    <va-minibar
        :theme="minibarTheme"
        :top-items="minibarTopItems"
        :bottom-items="minibarBottomItems" />
    <va-sidebar :theme="sidebarTheme">
      <va-sidebar-group :items="getStartedItems" :title="'Madaniy meros agentligi'" :defaultOpenLevel="1" />
      <va-sidebar-group :items="coreItems" :title="'Menu'" :defaultOpenLevel="0" />
    </va-sidebar>
  </div>
</template>
<script>
export default {
  data () {
    return {
      minibarTheme: 'white',
      minibarTopItems: [
        {
          icon: 'home',
          size: '1.5em',
          brand: true
        },
        {
          icon: 'search',
          size: '1.25em',
        },
      ],
      minibarBottomItems: [
        {
          icon: 'power-off',
          size: '1.5em',
          method: this.exit
        }
      ],

      sidebarTheme: 'dark',
      getStartedItems: [
        {
          name: 'Dashboard',
          route: '/',
        }
      ],
      coreItems: [
        {
          name: 'Products',
          children: [

            {
              name: 'Product Category',
              route: '/product-category',
            },
              {
              name: 'Product',
              route: '/products',
            },
          ]
        }
      ]
    }
  },
  methods: {
    exit(){
      localStorage.removeItem('reg_token')
      this.$router.push('/login')
    },
    demoMethod () {
      this.VaModal.confirm({
        title: 'Hello!',
        message: `This modal exists to show how methods
        can be applied to Minibar items.`,
        type: 'info'
      })
    },

  },
 }
</script>
<style>
.va-sidebar{
  width: 300px !important;
  left: 80px !important;
}
.va-minibar{
  width: 80px !important;
}
</style>
