import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      requiresAuth: true
    },
  },
  {
    path: '/product-category',
    name: 'Products',
    component: () => import(/* webpackChunkName: "about" */ '../views/productsCategory/index'),
    meta: {
      requiresAuth: true
    },
  },
  {
    path: '/product-category/create',
    name: 'Products',
    component: () => import(/* webpackChunkName: "about" */ '../views/productsCategory/create'),
    meta: {
      requiresAuth: true
    },
  },
  {
    path: '/product-category/edit/:id',
    name: 'Products',
    component: () => import(/* webpackChunkName: "about" */ '../views/productsCategory/edit'),
    meta: {
      requiresAuth: true
    },
  },
  {
    path: '/products',
    name: 'Products',
    component: () => import(/* webpackChunkName: "about" */ '../views/products/index'),
    meta: {
      requiresAuth: true
    },
  },
  {
    path: '/organizations/:organization_pk/product-categories/:category_pk/products/',
    name: 'Products',
    component: () => import(/* webpackChunkName: "about" */ '../views/products/products_child/index'),
    meta: {
      requiresAuth: true
    },
  },
  {
    path: '/product-categories/:category_pk/products/create',
    name: 'Products',
    component: () => import(/* webpackChunkName: "about" */ '../views/products/products_child/create'),
    meta: {
      requiresAuth: true
    },
  },
  {
    path: '/product-categories/:category_pk/products/:id/edit',
    name: 'Products',
    component: () => import(/* webpackChunkName: "about" */ '../views/products/products_child/edit'),
    meta: {
      requiresAuth: true
    },
  },
  {
    path: '/login',
    name: 'Login',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/auth/Login')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    // this route requires auth, check if logged in
    // if not, redirect to login page.
    if (!localStorage.getItem('reg_token')) {
      next({ name: 'Login' })

    } else {
      next() // go to wherever I'm going
    }
  } else {
    next() // does not require auth, make sure to always call next()!
  }
});
export default router
