<template>
  <div>
    <b-navbar toggleable="lg" type="dark" variant="white">
      <div>
        <h2>{{usersme.organization_title}}</h2>
      </div>
      <div class="logoin_mains">
        <div class="user_name">
          {{usersme.first_name}} {{usersme.last_name}}
        </div>
        <div data-v-6f48f880="" class="c-avatar">

          <img data-v-6f48f880="" src="data:image/png;base64,"
                                                      class="c-avatar-img">
        </div>
      </div>
    </b-navbar>
  </div>
</template>

<script>
export default {
  data () {
    return {
      usersme:{},
    }
  },
  mounted() {
    this.$axiosDefault.get(`me/`,
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
            'Accept-Language':"oz"
          }
        }
    )
    .then(res=>{
      this.usersme=res.data
    })


  }
}
</script>
